<script>
import { useComplicationFormInit } from "@/composable/born";
import { onMounted, watch } from "vue-demi";

export default {
    setup() {
        const {
            form,
            save,
            complications,
            fillForm,
            loading,
            complicationOptions,
            treatmentOutcomeOptions,
            typeOfPregnancyOptions,
            chorionicityOptions,
            electiveFetalReductionOptions,
            pregnancyBirthOutcomeOptions
        } = useComplicationFormInit();

        watch(complications, fillForm);

        onMounted(() => {
            if (complications.value) {
                fillForm(complications.value);
            }
        });

        return {
            form,
            save,
            loading,
            complicationOptions,
            treatmentOutcomeOptions,
            typeOfPregnancyOptions,
            chorionicityOptions,
            electiveFetalReductionOptions,
            pregnancyBirthOutcomeOptions
        };
    }
};
</script>
